import Dieline from './dieline'

class ShippingBoxDieline extends Dieline {

  constructor(args) {
    super(args)
    this.style = "Shipping Box"
  }

  rectangles() {

    return [
      {
        width: 4 * this.length + 14 * this.thickness,
        length: this.depth + this.width + 5 * this.thickness
      },
    ]
  }

}

export default ShippingBoxDieline
